import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  firstname: String;
  lastname: String;
  phone: String;
  email: String;

  constructor(private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const user = {
      firstname: this.firstname,
      lastname: this.lastname,
      phone: this.phone,
      email: this.email
    }

    const timeOut = 3000;

    if (!this.validateService.onRegisterSubmit(user)) {
      this.flashMessage.show('Please fill all fields', { cssClass: 'alert-danger', timeout: timeOut });
      return false;
    }

    if (!this.validateService.validateEmail(user.email)) {
      this.flashMessage.show('Enter Valid Email', { cssClass: 'alert-danger', timeout: timeOut });
      return false;
    }

    this.authService.registerUser(user).subscribe(data => {
      if (data.success) {
        this.flashMessage.show('New user is registered', { cssClass: 'alert-success', timeout: timeOut });
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Something went wrong', { cssClass: 'alert-danger', timeout: timeOut });
        this.router.navigate(['/register']);
      }
    });
  }
}



